.navbarContainer{
    position:absolute;
    display:flex;
    width: 100%;
    justify-content: space-between;
    background-color:transparent;
    color:var(--white);
    padding: 20px 10px;
}

.navbarBrand{
   padding: 10px;
    h2{ 
        // margin:50% 10%;
        font-size: 60px;
    }
}


.navbarLinks{
    width: 50%;
    display: flex;
    justify-content: center;
    hr{
        border:         none;
        border-left:    1px solid var(--gray);
        height:         80%;
        width:          1px;
        margin: 5px;       
    }
    .navbarLink{
        display: flex;
        flex-direction: column;
        justify-content: center;
        margin:0 15px;
    }
    span{
        align-self: center;
        font-size: 50px;
        height: 68px;
    }
    a{
        font-size: 18px;
        color: var(--white);
        text-transform: uppercase;
        margin: 3px 0;
    }
    }

.navbarUser{
    display: flex;
    flex-direction: row;
    margin: 15px;

    a, span{
        color: var(--white);
        margin:5px 10px;
        text-transform: uppercase;
    }
    hr{
        border:         none;
        border-left:    1px solid hsla(200, 10%, 50%,100);
        height:         50%;
        width:          1px;
   
    }

}

